import React, {Component} from "react";
import PropTypes from 'prop-types'
import OraTooltip from "eshop/modules/core/components/ui/Tooltip";
import OraHtmlText from "eshop/modules/core/components/ui/OraHtmlText";

export default class InfoWithTooltip extends Component {

    render() {
        return (
            <div>
                <p className="u-font-bold u-font-small">
                    <OraHtmlText>
                        {this.props.label}
                    </OraHtmlText>
                    {this.props.tooltip &&
                        <OraTooltip>
                            <OraHtmlText>
                                {this.props.tooltip}
                            </OraHtmlText>
                        </OraTooltip>
                    }
                </p>
            </div>
        );
    }
}

InfoWithTooltip.propTypes = {
  label: PropTypes.string,
  tooltip: PropTypes.string
};

